import Vue from 'vue'
import Router from 'vue-router'
//1.  vue  vue-Router


import index from './routes/index.vue'
import play from './routes/play.vue'

//2. 

//3.
Vue.use(Router)

//4.

var routes = [
	{
		path:'/',
		name:'index',
		component:index,
	},
	{
		path:'/play',
		name:'play',
		component:play,
	},
]


//5.
export default new Router({
	routes
})


