import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

var state = {
	arr:[],
	id:'',
	
}

//
var getters = {
	
}

//
var mutations = {
	play_list(state,num){
		var type = true;
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].songname == num.songname) {
				type = false
			}
		}
		if (type) {
			state.arr.push(num)
		}
		state.id = num
	},
	chang_list(state,aa){
		state.id = aa
	}
}

//
var actions = {
	play({commit}, num) {
		commit('play_list', num)
		location.href = "#/play"
	},
	change({commit},aa){
		commit('chang_list',aa)
	}
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})


