<template>
	<div id="app">
		<div class="top">
			<a href="#/"></a>
			<a href="#/play"></a>
		</div>
		<div class="cont">
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</div>
		<div class="bottom">
				<my_audio></my_audio>
		</div>
	</div>
</template>

<script>
	import my_audio from './c/audio.vue'
	export default {
		components: {
			my_audio
		},
		name: 'app',
		data() {
			return {

			}
		}
	}
</script>

<style>
	* {
		margin: 0px;
		padding: 0px;
		list-style: none;
		text-decoration: none;
		box-sizing: border-box;
	}

	.top {
		width: 100%;
		height: 60px;
		background: yellowgreen;
		color: #fff;
		line-height: 60px;
		display: flex;
	}

	.top a {
		color: #fff;
		flex: 1;
		text-align: center;
	}

	.top a:nth-child(1) {
		border-right: 1px solid #fff;
	}

	.cont {
		min-height: 520px;
	}

	.bottom {
		width: 100%;
		height: 150px;
		background: yellowgreen;
		text-align: center;
		padding-top: 30px;
	}
</style>



