<template>
	
	<div style="padding: 20px;">
		<div v-if="$store.state.arr.length">
			<div v-for="i in $store.state.arr" class="songname" @click="change(i)" :style="$store.state.id.songmid==i.songmid?{'color':'red'}:{'color':''}">
				{{i.songname}}
			</div>
		</div>
		<div v-else>
			
		</div>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		methods: {
			...mapActions(['change']),
		}
	}
</script>

<style>
	.songname{
		line-height: 40px;
		border-bottom: 1px solid #ccc;
		cursor: pointer;
	}
</style>



