<template>
	<div>
		<audio :src="songurl" controls autoplay></audio>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		data(){
			return{
				vkey:'',
				filename:'',
				songurl:''
			}
		},
		watch:{
			'$store.state.id'(a){
				console.log(a)
				this.$http.jsonp('https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?g_tk=1278911659&hostUin=0&format=jsonp&callback=callback&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&cid=205361747&uin=0&songmid=' + a.songmid + '&filename=C400' + a.songmid + '.m4a&guid=3655047200', {
					jsonpCallback: 'callback'
				}).then((data) => {
					console.log(data.data)
					this.vkey = data.data.data.items[0].vkey
					this.filename = data.data.data.items[0].filename
					this.songurl = `
				 http://dl.stream.qqmusic.qq.com/${this.filename}?vkey=${this.vkey}&guid=3655047200&fromtag=66
				 `
				})
			}
		},
		
	}
</script>

<style>
</style>




