<template>
	<div>
		<!--  -->
		<div style="text-align: center;">
			<input type="text" placeholder=" " class="my_ipt" @keyup.enter="sou()" v-model="value" >
		</div>

		<!--  -->
		<div class="list_cont" v-if="songlist">
			<h5></h5>
			<div class="list_song" v-for="i in songlist" @click="play(i)" :style="$store.state.id.songmid==i.songmid?{'color':'red'}:{'color':''}">
				<p>{{i.songname}}</p>
			</div>
		</div>

	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
	export default {
		data() {
			return {
				value: '',
				songlist: ''
			}
		},
		methods: {
			sou() {
				this.$http.jsonp('https://c.y.qq.com/soso/fcgi-bin/client_search_cp?g_tk=5381&p=1&n=20&w=' + this.value +
					'&format=jsonp&jsonpCallback=callback&loginUin=0&hostUin=0&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&remoteplace=txt.yqq.song&t=0&aggr=1&cr=1&catZhida=1&flag_qc=0', {
						jsonpCallback: 'callback'
					}).then((data) => {
					// console.log(data)
					this.songlist = data.data.data.song.list
					console.log(this.songlist)
				})
			},
			...mapActions(['play'])

		}
	}
</script>

<style>
	.my_ipt {
		margin-top: 30px;
		width: 50%;
		height: 40px;
		padding-left: 20px;
		line-height: 40px;
	}

	.list_cont {
		text-align: left;
		width: 52%;
		overflow-y: scroll;
		margin: 20px 24%;
	}
	.list_cont h5{
		line-height: 40px;
		font-weight: normal;
		font-size: 20px;
	}
	.list_song{
		line-height: 40px;
		border-bottom: 1px solid #ccc;
		padding-left: 10px;
		cursor: pointer;
	}
	
</style>



